*,
::after,
::before {

	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

body {
  overflow-x: hidden;
  font-family: $fonts-main-page;
  color: $text-color-dark;
  background-color: $primary-white;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
  padding: 0;
  margin: 0;
}

.container {
	margin-right: auto;
	margin-left: auto;
	padding-right: 20px;
	padding-left: 20px;
	width: 320px;

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 34px;
    padding-right: 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-right: 117px;
    padding-left: 117px;
  }
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
  width: 100%;
}

.section__title {
  display: flex;
  justify-content: flex-start;
  width: 200px;
  margin-top: 0;
  margin-bottom: 0;
  font-family: $fonts-title;
  font-weight: 400;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $accent-color;

  @media screen and (min-width: 768px) {
    min-width: 340px;
    font-size: 48px;
    line-height: 55px;
    letter-spacing: 0.06em;
  }

  @media screen and (min-width: 1280px) {
    width: 534px;
    font-size: 58px;
    line-height: 66px;
  }
}

.section__subtitle {
  margin-top: 0;
  margin-bottom: 17px;
  max-width: 232px;
  font-family: $fonts-title;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  color: $title-color-pink;

  @media screen and (min-width: 768px) {
    max-width: 340px;
    margin-bottom: 16px;
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: 1280px) {
    max-width: 472px;
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 34px;
  }
}

.animated:hover,
.animated:focus {
  animation-name: headShake;
  animation-duration: 0.7s;
  animation-timing-function: ease;
}
